button {
  text-transform: uppercase;
  color: rgba(255, 255, 255, .4);
  letter-spacing: 2px;
  cursor: pointer;
  background: #262c37;
  padding: 10px 30px;
  font-size: 16px;
  text-decoration: none;
  transition: all .5s;
  display: inline-block;
  position: relative;
}

button:hover {
  color: #fff;
}

button span {
  background: #2894ff;
  display: block;
  position: absolute;
}

button span:nth-child(1) {
  width: 1.5px;
  height: 100%;
  transform-origin: top;
  transition: transform .5s;
  bottom: 0;
  left: 0;
  transform: scaleY(0);
}

button:hover span:nth-child(1) {
  transform-origin: bottom;
  transition: transform .5s;
  transform: scaleY(1);
}

button span:nth-child(2) {
  width: 100%;
  height: 1.5px;
  transform-origin: 100%;
  transition: transform .5s;
  bottom: 0;
  left: 0;
  transform: scaleX(0);
}

button:hover span:nth-child(2) {
  transform-origin: 0;
  transition: transform .5s;
  transform: scaleX(1);
}

button span:nth-child(3) {
  width: 1.5px;
  height: 100%;
  transform-origin: top;
  transition: transform .5s .5s;
  bottom: 0;
  right: 0;
  transform: scaleY(0);
}

button:hover span:nth-child(3) {
  transform-origin: bottom;
  transition: transform .5s .5s;
  transform: scaleY(1);
}

button span:nth-child(4) {
  width: 100%;
  height: 1.5px;
  transform-origin: 100%;
  transition: transform .5s .5s;
  top: 0;
  left: 0;
  transform: scaleX(0);
}

button:hover span:nth-child(4) {
  transform-origin: 0;
  transition: transform .5s .5s;
  transform: scaleX(1);
}

button:disabled {
  cursor: default;
  pointer-events: none;
  background-color: rgba(255, 255, 255, .4);
}

